/* login page */
/* animations */
[class*="uk-animation-"] {
  animation-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  animation-duration: 560ms;
}
/* alerts */
.uk-alert {
  border: none;
  background: #1976d2;
  color: #fff;
  text-shadow: none;
  padding-right: 32px;
  position: relative;
  display: block;
}
.uk-alert-success {
  background: #8bc34a !important;
}
.uk-alert-danger {
  background: #e53935 !important;
}
.uk-alert-warning {
  background: #ffa000 !important;
}
.uk-alert-info {
  background: #0097a7 !important;
}
.uk-alert-large {
  padding: 24px 32px 24px 24px;
}
.uk-alert-close {
  position: absolute;
  top: 10px;
  right: 8px;
  float: none !important;
  margin: 0 !important;
}
.uk-alert-close:after {
  color: #fff !important;
}
/* badges */
.uk-badge {
  background: #0097a7;
  padding: 2px 6px;
  border: none;
  border-radius: 2px;
  text-shadow: none;
  font-size: 11px;
  line-height: 15px;
  font-weight: 400;
  white-space: nowrap;
}
.uk-badge-primary {
  background: #2196f3;
}
.uk-badge-danger {
  background: #e53935;
}
.uk-badge-warning {
  background: #ffa000;
}
.uk-badge-success {
  background: #7cb342;
}
.uk-badge-muted {
  background: #bdbdbd;
}
.uk-badge-notification {
  border-radius: 20px;
}
.uk-badge.inline-label {
  vertical-align: 1px;
  margin-left: 4px;
}
.uk-badge-outline {
  border: 1px solid rgba(0, 0, 0, 0.12);
  background: #fff;
  color: #212121;
  padding-bottom: 0;
  line-height: 16px;
}
/* buttons */
.uk-button {
  font-weight: 400;
  border-radius: 2px;
  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
}
/* comments */
.uk-comment-list > li + li {
  border-top: 1px dashed rgba(0, 0, 0, 0.12);
  margin-top: 16px !important;
}
.uk-comment-list .uk-comment + ul {
  margin: 8px 0 0 0;
}
@media only screen and (min-width: 768px) {
  .uk-comment-list .uk-comment + ul {
    padding-left: 48px;
  }
}
.uk-comment-header {
  padding: 16px 8px 8px;
  border: none;
  background: none;
  margin-bottom: 0;
}
.uk-comment-title {
  margin: 0;
  font-size: 14px;
  line-height: 20px;
}
/* dialog modals */
.uk-modal {
  z-index: 1304;
  background: rgba(0, 0, 0, 0.5);
  transition: opacity 250ms ease-out;
  overflow: auto !important;
}
.uk-modal.uk-modal-no-backdrop {
  background: transparent;
}
.uk-modal-dialog {
  border-radius: 2px;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.22);
  padding: 24px;
  display: block !important;
  animation: none !important;
  transition: transform 280ms cubic-bezier(0.4, 0, 0.2, 1), opacity 280ms ease-in;
  transform: scale(0);
}
.uk-modal-dialog-blank {
  padding: 0;
  background: none;
  box-shadow: none;
}
.uk-open .uk-modal-dialog {
  transform: scale(1);
}
.uk-modal-dialog .uk-modal-header {
  background: none;
  border-bottom: none;
  margin: 0 -24px 24px;
  padding: 0 32px 0 24px;
  overflow: hidden;
}
.uk-modal-dialog .uk-modal-header .uk-modal-title {
  margin: 0;
  font: 500 18px / 28px "Roboto", sans-serif;
}
.uk-modal-dialog .uk-modal-header .uk-modal-title span {
  font-size: 16px;
  display: block;
  color: #727272;
}
.uk-modal-dialog .uk-modal-header .material-icons {
  font-size: 24px;
  vertical-align: -4px;
  cursor: default;
}
.uk-modal-dialog .uk-modal-footer {
  margin: 16px -16px -16px;
  padding: 16px;
  background: #fff;
  border-top: none;
}
.uk-modal-dialog .uk-modal-footer:before,
.uk-modal-dialog .uk-modal-footer:after {
  content: " ";
  display: table;
}
.uk-modal-dialog .uk-modal-footer:after {
  clear: both;
}
.uk-modal-dialog .uk-modal-footer:before,
.uk-modal-dialog .uk-modal-footer:after {
  content: " ";
  display: table;
}
.uk-modal-dialog .uk-modal-footer:after {
  clear: both;
}
.uk-modal-dialog .uk-modal-footer .md-icon-btn {
  margin-top: 2px;
}
.uk-modal-dialog .uk-modal-caption {
  bottom: 16px;
  margin: 0 32px;
}
.uk-modal-dialog > .uk-close:first-child {
  top: 8px;
  right: 8px;
  position: absolute;
  float: none;
  margin: 0;
}
.uk-modal-dialog-lightbox {
  padding: 0;
}
.uk-modal-dialog-lightbox > .uk-close:first-child {
  top: -11px;
  right: -11px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
  border: none;
}
.uk-modal-dialog .uk-overflow-container {
  margin: 16px 0;
}
.uk-modal-card-fullscreen {
  overflow: hidden !important;
}
.uk-modal-card-fullscreen .uk-modal-close {
  float: left;
  margin: 8px 8px 0 0;
  display: block;
}
.uk-modal-card-fullscreen > .uk-modal-dialog > .md-card > .md-card-toolbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  z-index: 10;
}
.uk-modal-card-fullscreen > .uk-modal-dialog > .md-card > .md-card-content {
  position: absolute;
  top: 48px;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-y: auto;
}
.uk-modal .uk-margin.uk-modal-content {
  margin-bottom: 0;
}
.uk-modal.uk-modal-dialog-replace .uk-modal-content {
  font-size: 18px;
}
.uk-modal-spinner {
  animation: uk-rotate 2s infinite linear;
  margin-left: -12px;
  margin-top: -17px;
  left: 50%;
  right: auto;
}
/* dropdowns */
.uk-dropdown {
  transform: scale(0.25, 0);
  opacity: 0;
  transition: all 280ms cubic-bezier(0.4, 0, 0.2, 1);
  animation: none !important;
  transform-origin: 50% 0 !important;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  border: none;
  border-radius: 2px;
}
.uk-dropdown.uk-dropdown-xlarge {
  width: 360px;
}
@media only screen and (max-width: 479px) {
  .uk-dropdown.uk-dropdown-xlarge {
    width: 260px;
  }
}
.uk-dropdown.uk-dropdown-large {
  width: 260px;
}
.uk-dropdown.uk-dropdown-small {
  width: 160px;
}
.uk-dropdown > ul > li > a {
  font-size: 14px;
  color: #212121;
}
.uk-dropdown > ul > li > a:hover {
  background: rgba(0, 0, 0, 0.085);
}
.uk-dropdown > ul > li.uk-active > a {
  background: rgba(0, 0, 0, 0.085);
}
.uk-dropdown > ul > li.padding_sm {
  padding: 2px 4px;
}
.uk-dropdown > ul > li.padding_md {
  padding: 4px 8px;
}
.uk-dropdown.dropdown-modal {
  z-index: 1310;
}
.uk-dropdown.dropdown-fs {
  z-index: 9999;
}
.uk-dropdown.uk-dropdown-scrollable {
  -webkit-overflow-scrolling: touch;
}
[data-uk-dropdown*="top-"] .uk-dropdown {
  transform-origin: 50% 100% !important;
}
[data-uk-dropdown*="left-"] .uk-dropdown {
  transform-origin: 100% 50% !important;
}
[data-uk-dropdown*="right-"] .uk-dropdown {
  transform-origin: 0 50% !important;
}
[data-uk-dropdown*='justify'] {
  position: static !important;
}
[data-uk-dropdown*='justify'] [class*="uk-dropdown-width"] {
  left: 0 !important;
  width: 100% !important;
  min-width: inherit !important;
  margin-left: 0 !important;
}
.uk-dropdown-active {
  display: block !important;
}
.uk-dropdown-shown {
  transform: scale(1, 1);
  opacity: 1;
}
.uk-nav-dropdown > li > a:focus,
.uk-nav-dropdown > li > a:hover {
  text-shadow: none;
  box-shadow: none;
  color: #212121;
  background: rgba(153, 153, 153, 0.2);
}
/* forms */
fieldset {
  border: none;
  margin: 0;
}
fieldset + * {
  margin-top: 15px;
}
.uk-form input:not([type]),
.uk-form input[type=color],
.uk-form input[type=date],
.uk-form input[type=datetime-local],
.uk-form input[type=email],
.uk-form input[type=month],
.uk-form input[type=number],
.uk-form input[type=password],
.uk-form input[type=search],
.uk-form input[type=tel],
.uk-form input[type=text],
.uk-form input[type=time],
.uk-form input[type=url],
.uk-form input[type=week],
.uk-form select,
.uk-form textarea {
  box-sizing: border-box;
  padding: 8px;
  border: 1px solid rgba(0, 0, 0, 0.12);
  transition: border 200ms ease-in;
  resize: none;
}
.uk-form input:not([type]):focus,
.uk-form input[type=color]:focus,
.uk-form input[type=date]:focus,
.uk-form input[type=datetime-local]:focus,
.uk-form input[type=email]:focus,
.uk-form input[type=month]:focus,
.uk-form input[type=number]:focus,
.uk-form input[type=password]:focus,
.uk-form input[type=search]:focus,
.uk-form input[type=tel]:focus,
.uk-form input[type=text]:focus,
.uk-form input[type=time]:focus,
.uk-form input[type=url]:focus,
.uk-form input[type=week]:focus,
.uk-form select:focus,
.uk-form textarea:focus {
  background: none;
  border-color: #2196f3;
}
.uk-form textarea {
  transition: border-color 200ms ease-in, height 280ms ease-in;
}
.uk-form-row {
  transition: all 200ms ease-out;
}
.uk-form-row + .uk-form-row {
  margin-top: 24px;
}
.uk-form-stacked .uk-form-label {
  font-weight: 500;
  font-size: 13px;
  display: block;
  padding-bottom: 8px;
}
.uk-form-stacked .uk-form-label + p {
  margin-top: 0;
}
.uk-form-help-block {
  display: block;
  font-size: 12px;
  color: #727272;
  padding: 4px 0 0 0;
  font-style: italic;
}
.uk-form-width-large,
.uk-form-width-medium,
.uk-form-width-small,
.uk-form-width-mini {
  max-width: 100%;
}
/* grid */
.uk-grid + .uk-grid,
.uk-grid-margin,
.uk-grid > * > .uk-panel + .uk-panel {
  margin-top: 24px;
}
.uk-grid.uk-grid-small + .uk-grid-small,
.uk-grid.uk-grid-small > * > .uk-panel + .uk-panel,
.uk-grid.uk-grid-small > .uk-grid-margin {
  margin-top: 10px;
}
.uk-height-viewport {
  min-height: 200px;
}
/* icons */
[class*="uk-icon-"] {
  color: #727272;
}
/* input groups */
.uk-input-group {
  position: relative;
  display: table;
  border-collapse: separate;
  /*&-icon {
        font-size: 20px !important;
        vertical-align: -3px;
    }*/
}
.uk-input-group > input[type=text] {
  position: relative;
  z-index: 2;
  float: left;
  width: 100%;
  margin-bottom: 0;
}
.uk-input-group-addon,
.uk-input-group > input[type=text] {
  display: table-cell;
}
.uk-input-group-addon {
  width: 1%;
  white-space: nowrap;
  vertical-align: middle;
  line-height: 1;
  text-align: center;
  padding: 0 16px;
  font-size: 16px;
  min-width: 22px;
}
.uk-input-group > .md-input-wrapper {
  display: inline-block;
}
.uk-input-group.uk-input-group-danger .uk-input-group-addon i {
  color: #e53935 !important;
}
.uk-input-group.uk-input-group-danger + .parsley-errors-list {
  margin-left: 58px;
}
.uk-input-group.uk-input-group-success .uk-input-group-addon i {
  color: #7cb342 !important;
}
/* list */
.uk-list-hover li:hover {
  background: rgba(0, 0, 0, 0.1);
}
/* nav */
.uk-nav-header {
  color: #727272;
  font-weight: normal;
}
.uk-nav-divider {
  box-shadow: none;
  border-top-color: rgba(0, 0, 0, 0.12);
}
.uk-nav-divider + .uk-nav-header {
  margin-top: 0;
}
.uk-nav-label {
  width: 12px;
  height: 12px;
  display: inline-block;
  vertical-align: -1px;
  margin-right: 8px;
}
.uk-nav-side > li > a {
  font-weight: 500;
  line-height: 22px;
}
.uk-nav-side > li > a > i {
  font-size: 24px;
  margin-right: 10px;
  vertical-align: -7px;
}
.uk-nav-side > li > a:hover,
.uk-nav-side > li > a:focus {
  background: transparent;
  box-shadow: none;
}
.uk-nav-side > li.uk-active > a {
  background: transparent;
  box-shadow: none;
}
.uk-nav-side > li.uk-active > a,
.uk-nav-side > li.uk-active > a > i {
  color: #7cb342;
}
/* pagination */
.uk-pagination {
  margin: 0;
}
.uk-pagination > li {
  outline: none !important;
}
.uk-pagination > li > a,
.uk-pagination > li > span {
  border: none;
  padding: 4px 8px;
  min-width: 32px;
  line-height: 24px;
  height: 32px;
  box-sizing: border-box;
  text-shadow: none;
  color: #212121;
  border-radius: 4px;
}
.uk-pagination > li > a [class*="uk-icon-"],
.uk-pagination > li > span [class*="uk-icon-"] {
  font-size: 16px;
}
.uk-pagination > li > a {
  background: transparent;
}
.uk-pagination > li > a:hover {
  background: #e1efd2;
}
.uk-pagination > li.uk-active > a,
.uk-pagination > li.uk-active > span {
  background: #7cb342;
}
.uk-pagination > li.uk-active > a,
.uk-pagination > li.uk-active > span,
.uk-pagination > li.uk-active > a:hover,
.uk-pagination > li.uk-active > span:hover {
  color: #fff;
}
.uk-pagination > li.uk-disabled > a,
.uk-pagination > li.uk-disabled > span {
  border: none;
  text-shadow: none;
  background: transparent !important;
  color: #aaa;
  cursor: default;
}
/* panels */
.uk-panel-box {
  border-radius: 2px;
  background: #fff;
  border-color: rgba(0, 0, 0, 0.12);
}
.uk-panel-box .uk-panel-teaser {
  border-radius: 2px 2px 0 0;
}
/* tables */
.uk-table td {
  border-bottom-color: rgba(0, 0, 0, 0.12);
}
.uk-table th {
  border-bottom: 1px #444;
}
.uk-table-nowrap td,
.uk-table-nowrap th {
  white-space: nowrap;
}
.uk-table-align-vertical td,
.uk-table-align-vertical th {
  vertical-align: middle;
}
.uk-table tr th.small_col,
.uk-table tr td.small_col {
  width: 1%;
}
.uk-table tr.row_checked td {
  background: #e3f2fd;
}
.uk-table .uk-table {
  margin-bottom: 0;
}
.uk-table .uk-table tr:last-child td {
  border-bottom: none;
}
.uk-table-no-border td {
  border-bottom-color: transparent;
}
/* sticky */
.uk-sticky-placeholder .uk-active {
  z-index: 1094;
}
/* subnav */
.uk-subnav-pill > * > * {
  color: #212121;
}
.uk-subnav-pill > .uk-active > * {
  background: #7cb342;
}
/* tables */
.uk-table thead th {
  border-bottom: 2px solid rgba(0, 0, 0, 0.12);
}
.uk-table thead th,
.uk-table tfoot td,
.uk-table tfoot th {
  font-style: normal;
  font-weight: 400;
  color: #727272;
  font-size: 14px;
}
.uk-table td {
  border-bottom-color: #e0e0e0;
}
.uk-table tfoot td,
.uk-table tfoot th {
  border-top: 2px solid rgba(0, 0, 0, 0.12);
  border-bottom: none;
}
.uk-table-striped tbody tr:nth-of-type(odd) {
  background: rgba(0, 0, 0, 0.085);
}
.uk-table-hover tbody tr:hover {
  background: rgba(0, 0, 0, 0.085);
}
/* thumbnails */
.uk-thumbnail {
  border-radius: 0;
  border-color: rgba(0, 0, 0, 0.12);
}
.uk-thumbnail-caption {
  padding: 4px 4px 0;
  line-height: 20px;
  color: #727272;
  font-size: 12px;
}
/* utilities */
.uk-text-small {
  font-size: 12px;
}
.uk-text-muted {
  color: #757575 !important;
}
.uk-text-primary {
  color: #2196f3 !important;
}
.uk-text-danger {
  color: #e53935 !important;
}
.uk-text-success {
  color: #7cb342 !important;
}
.uk-text-warning {
  color: #ffa000 !important;
}
.uk-margin-bottom {
  margin-bottom: 16px !important;
}
.uk-margin-small-bottom {
  margin-bottom: 8px !important;
}
.uk-margin-medium-bottom {
  margin-bottom: 32px !important;
}
.uk-margin-large-bottom {
  margin-bottom: 48px !important;
}
.uk-margin-medium-top {
  margin-top: 32px !important;
}
.uk-margin-large-top {
  margin-top: 48px !important;
}
.uk-close {
  font-size: 18px;
  opacity: 1;
}
.uk-close:hover,
.uk-close:focus {
  opacity: 1;
}
.uk-close:after {
  opacity: 1 !important;
  color: #727272;
  content: '\e5cd';
  font-family: "Material Icons";
}
/* ------------ Components ------------ */
