}
.uk-accordion-title-warning:after {
  color: #fff;
}
.uk-accordion-content {
  padding: 16px;
}
.uk-accordion-alt .uk-accordion-title {
  background: #fff;
  margin: 0;
  padding: 16px 24px 16px 48px;
  border-top: 1px solid rgba(0, 0, 0, 0.12);
}
.uk-accordion-alt .uk-accordion-title:first-child {
